import { createRouter, createWebHistory } from 'vue-router'
import MainContainer from "@/views/MainContainer";
import CartContainer from "@/views/CartContainer";
import LoginContainer from "@/views/LoginContainer";
import FinishOrderContainer from "@/views/FinishOrderContainer";
import MyCouponsContainer from "@/views/MyCouponsContainer";

const routes = [
  {
    path: "/:id",
    component: MainContainer,
  },
  {
    path: "/:id/cart",
    component: CartContainer
  },
  {
    path: "/:id/login",
    component: LoginContainer
  },
  {
    path: "/:id/finish",
    component: FinishOrderContainer
  },
  {
    path: "/:id/coupon",
    component: MyCouponsContainer
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
